<template>
<div :style='{"border":"2px solid #dbd9f4","padding":"20px","margin":"10px auto","borderRadius":"8px","background":"#fff","width":"1200px","position":"relative"}'>
    <el-form
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="采购单号" prop="caigoudanhao">
              <el-input v-model="ruleForm.caigoudanhao" placeholder="采购单号" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="鲜花名称" prop="xianhuamingcheng">
            <el-input v-model="ruleForm.xianhuamingcheng" 
                placeholder="鲜花名称" clearable readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="鲜花图片" v-if="type!='cross' || (type=='cross' && !ro.xianhuatupian)" prop="xianhuatupian">
            <file-upload
            tip="点击上传鲜花图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.xianhuatupian?ruleForm.xianhuatupian:''"
            @change="xianhuatupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' class="upload" v-else label="鲜花图片" prop="xianhuatupian">
                <img v-if="ruleForm.xianhuatupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.xianhuatupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.xianhuatupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="供应公司" prop="gongyinggongsi">
            <el-input v-model="ruleForm.gongyinggongsi" 
                placeholder="供应公司" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="采购价格" prop="caigoujiage">
            <el-input v-model="ruleForm.caigoujiage" 
                placeholder="采购价格" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="采购数量" prop="alllimittimes">
            <el-input v-model="ruleForm.alllimittimes" 
                placeholder="采购数量" clearable ></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="订单金额" prop="dingdanjine">
              <el-input v-model="dingdanjine" placeholder="订单金额" readonly></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="采购日期" prop="caigouriqi" disabled>
              <el-date-picker
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.caigouriqi" 
                  type="date"
                  placeholder="采购日期">
              </el-date-picker> 
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"0"}' label="采购工号" prop="caigougonghao">
            <el-input v-model="ruleForm.caigougonghao" 
                placeholder="采购工号" clearable readonly></el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 25%","outline":"none","color":"#666","borderRadius":"10px","background":"linear-gradient(90deg, rgba(255,233,100,1) 0%, rgba(194,248,126,1) 29%, rgba(181,233,252,1) 61%, rgba(246,172,218,1) 100%)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"2px solid rgba(213, 203, 235, 1)","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#000","borderRadius":"4px","background":"rgba(255, 255, 255, 1)","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
      return {
        id: '',
        baseUrl: '',
        ro:{
            caigoudanhao : false,
            xianhuamingcheng : false,
            xianhuatupian : false,
            gongyinggongsi : false,
            caigoujiage : false,
            alllimittimes : false,
            dingdanjine : false,
            caigouriqi : false,
            caigougonghao : false,
            shhf : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          caigoudanhao: this.getUUID(),
          xianhuamingcheng: '',
          xianhuatupian: '',
          gongyinggongsi: '',
          caigoujiage: '',
          alllimittimes: '',
          dingdanjine: '',
          caigouriqi: '',
          caigougonghao: '',
        },


        rules: {
          caigoudanhao: [
          ],
          xianhuamingcheng: [
          ],
          xianhuatupian: [
          ],
          gongyinggongsi: [
          ],
          caigoujiage: [
            { required: true, message: '采购价格不能为空', trigger: 'blur' },
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          alllimittimes: [
            { required: true, message: '采购数量不能为空', trigger: 'blur' },
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          dingdanjine: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          caigouriqi: [
          ],
          caigougonghao: [
            { required: true, message: '采购工号不能为空', trigger: 'blur' },
          ],
          shhf: [
          ],
        },
      };
    },
    computed: {


        dingdanjine:{
            get: function () {
                return 1*this.ruleForm.caigoujiage*this.ruleForm.alllimittimes
            }
        },

    },
    components: {
    },
    created() {
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.caigouriqi = this.getCurDate()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='caigoudanhao'){
              this.ruleForm.caigoudanhao = obj[o];
              this.ro.caigoudanhao = true;
              continue;
            }
            if(o=='xianhuamingcheng'){
              this.ruleForm.xianhuamingcheng = obj[o];
              this.ro.xianhuamingcheng = true;
              continue;
            }
            if(o=='xianhuatupian'){
              this.ruleForm.xianhuatupian = obj[o].split(",")[0];
              this.ro.xianhuatupian = true;
              continue;
            }
            if(o=='gongyinggongsi'){
              this.ruleForm.gongyinggongsi = obj[o];
              this.ro.gongyinggongsi = true;
              continue;
            }
            if(o=='caigoujiage'){
              this.ruleForm.caigoujiage = obj[o];
              this.ro.caigoujiage = true;
              continue;
            }
            if(o=='alllimittimes'){
              this.ruleForm.alllimittimes = obj[o];
              this.ro.alllimittimes = true;
              continue;
            }
            if(o=='dingdanjine'){
              this.ruleForm.dingdanjine = obj[o];
              this.ro.dingdanjine = true;
              continue;
            }
            if(o=='caigouriqi'){
              this.ruleForm.caigouriqi = obj[o];
              this.ro.caigouriqi = true;
              continue;
            }
            if(o=='caigougonghao'){
              this.ruleForm.caigougonghao = obj[o];
              this.ro.caigougonghao = true;
              continue;
            }
          }
          this.ruleForm.alllimittimes = 0
        }
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.caigougonghao!=''&&json.caigougonghao) || json.caigougonghao==0){
                this.ruleForm.caigougonghao = json.caigougonghao
            }
          }
        });
      },

    // 多级联动参数
      // 多级联动参数
      info(id) {
        this.$http.get('rukuxinxi/detail/${id}', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {

          this.ruleForm.dingdanjine = this.dingdanjine
        var obj = JSON.parse(localStorage.getItem('crossObj'));
        var table = localStorage.getItem('crossTable');
        obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

        //this.$http.post(table+`/update`, obj).then(res => {});
        //更新跨表属性
        var crossuserid;
        var crossrefid;
        var crossoptnum;
        this.$refs["ruleForm"].validate(valid => {
          if(valid) {
            if(this.type=='cross'){
                 var statusColumnName = localStorage.getItem('statusColumnName');
                 var statusColumnValue = localStorage.getItem('statusColumnValue');
                 if(statusColumnName && statusColumnName!='') {
                     var obj = JSON.parse(localStorage.getItem('crossObj'));
                     if(!statusColumnName.startsWith("[")) {
                         for (var o in obj){
                             if(o==statusColumnName){
                                 obj[o] = statusColumnValue;
                             }
                         }
                         var table = localStorage.getItem('crossTable');
                         this.$http.post(table+'/update', obj).then(res => {});
                     } else {
                            crossuserid=Number(localStorage.getItem('userid'));
                            crossrefid=obj['id'];
                            crossoptnum=localStorage.getItem('statusColumnName');
                            crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                     }
                 }
            }
            if(crossrefid && crossuserid) {
                 this.ruleForm.crossuserid=crossuserid;
                 this.ruleForm.crossrefid=crossrefid;
                 var params = {
                     page: 1,
                     limit: 10,
                     crossuserid:crossuserid,
                     crossrefid:crossrefid,
                 }
                 this.$http.get('rukuxinxi/list', {
                  params: params
                 }).then(res => {
                     if(res.data.data.total>=crossoptnum) {
                         this.$message({
                          message: localStorage.getItem('tips'),
                          type: 'success',
                          duration: 1500,
                         });
                          return false;
                     } else {
                         // 跨表计算
                          var obj = JSON.parse(localStorage.getItem('crossObj'));
                          var table = localStorage.getItem('crossTable');

                          obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

                          this.$http.post(table+`/update`,obj).then(res => {});
                          this.$http.post('rukuxinxi/add', this.ruleForm).then(res => {
                              if (res.data.code == 0) {
                                  this.$message({
                                      message: '操作成功',
                                      type: 'success',
                                      duration: 1500,
                                      onClose: () => {
                                          this.$router.go(-1);
                                      }
                                  });
                              } else {
                                  this.$message({
                                      message: res.data.msg,
                                      type: 'error',
                                      duration: 1500
                                  });
                              }
                          });
                     }
                 });
             } else {
                  var obj = JSON.parse(localStorage.getItem('crossObj'));
                  var table = localStorage.getItem('crossTable');

                  obj.alllimittimes = parseFloat(obj.alllimittimes) + parseFloat(this.ruleForm.alllimittimes)

                  this.$http.post(table+`/update`,obj).then(res => {});
                  this.$http.post('rukuxinxi/add', this.ruleForm).then(res => {
                     if (res.data.code == 0) {
                          this.$message({
                              message: '操作成功',
                              type: 'success',
                              duration: 1500,
                              onClose: () => {
                                  this.$router.go(-1);
                              }
                          });
                      } else {
                          this.$message({
                              message: res.data.msg,
                              type: 'error',
                              duration: 1500
                          });
                      }
                  });
             }
          }
        });
      },
      // 获取uuid
      getUUID () {
        return new Date().getTime();
      },
      // 返回
      back() {
        this.$router.go(-1);
      },
      xianhuatupianUploadChange(fileUrls) {
          this.ruleForm.xianhuatupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");;
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
